import { MyTheme } from '@utils/interface';
import Link from 'next/link';
import React from 'react';
import styled, { ThemeProps } from 'styled-components';

export interface IAnchorProps extends MyTheme {
  highlight: string;
  href: string;
  withNextRoute?: boolean;
}

const AnchorWrapper = styled.div<MyTheme>`
  a {
    color: inherit;
    text-decoration: none;
  }
  .highlighter {
    height: 2px;
    transform: scale(0.4, 1);
    color: white;
  }
`;

// Will use `useMemo` later on...
export const Anchor: React.FC<IAnchorProps> = props => (
  <AnchorWrapper>
    {!!props.withNextRoute ? (
      <Link href={props.href}>
        <a>{props.children}</a>
      </Link>
    ) : (
      <a href={props.href}>{props.children}</a>
    )}
    <div className='higlighter' />
  </AnchorWrapper>
);
